$text-primary-color: #FFFFFF;
$text-secondary-color: #D4D3D3;
$text-info-color: #7D7C7A;

$panel-color: #272522;
$panel-heading-color: #21201D;
$btn-color: #201E1C;
$btn-confirm-color: #7FA650;
$btn-secondary-color: #383734;

